

































import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import FormItem from '@/components/Money/FormItem.vue';
import Button from '@/components/Button.vue';

@Component({
  components: {Button, FormItem}
})
export default class EditRecords extends Vue {
  temp = '';

  get record() {
    return this.$store.state.currentRecord;
  }

  created() {
    this.$store.commit('fetchRecords');
    const date = this.$route.params.id;
    this.$store.commit('setCurrentRecord', date);
    if (this.record) {
      return;
    } else {
      this.$router.replace('/404');
    }
  }

  tagString(tags: Tag[]) {
    const arrName = [];
    for (let i = 0; i < tags.length; i++) {
      arrName.push(tags[i].name);
    }
    return arrName.length === 0 ? '无' : arrName.join('，');
  }

  goBack() {
    this.$router.replace('/Statistics');
  }

  updateRecordListTags(val: string) {
    this.temp = val;
  }

  remove() {
    this.$store.commit('removeRecord');
    this.$router.replace('/Statistics');
  }

  save() {
    if(this.temp){
      this.record.tags[0].name = this.temp;
      this.$store.commit('createTag', this.temp);
      this.$store.commit('updateRecord');
      this.$router.replace('/Statistics');
    }else{
      window.alert('标签不能为空')
    }
  }
}
